import React, { Component } from 'react'
import { Calendar } from 'antd'
import '../css/calendar.css'

class CalendarComponent extends Component {
  state = {
    months: [
      { m: 1, y: 1 },
    ],
    testMonths: null
  }

  addImageBackground = value => {
    let months = value.month();
    this.setState({ testMonths: months });
  }

  render () {
    // console.log("STATE: ", this.state.testMonths);
    return (
      <div>
        <Calendar dateCellRender={this.addImageBackground} />
      </div>
    )
  }
}

export default CalendarComponent
